<template>
  <div class="contact-form" :class="{ 'night-mode': isNightMode, 'day-mode': !isNightMode }">
    <h2>Vous souhaitez organiser une rencontre?</h2>
    <form @submit.prevent="submitForm">
      <div class="form-group">
        <label for="name">Votre nom</label>
        <input type="text" id="name" v-model="formData.name" required />
      </div>
      <div class="form-group">
        <label for="email">Votre e-mail</label>
        <input type="email" id="email" v-model="formData.email" required />
      </div>
      <div class="form-group">
        <label for="subject">Quel est l'objet de votre message?</label>
        <select id="subject" v-model="formData.subject" required>
          <option value="" disabled>Sélectionnez le sujet</option>
          <option value="Enregistrement et Captation Sonore">Enregistrement et Captation Sonore</option>
          <option value="Mixage et Traitement Sonore">Mixage et Traitement Sonore</option>
          <option value="Correction et Amélioration Technique">Correction et Amélioration Technique</option>
          <option value="Autre">Autre</option>
        </select>
      </div>
      <div class="form-group">
        <label for="request">Votre projet</label>
        <textarea id="request" v-model="formData.request" required></textarea>
      </div>
      <button :disabled="!(this.formData.name&&this.formData.email&&this.formData.subject&&this.formData.request)" type="submit">Envoyer</button>
    </form>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import emailjs from 'emailjs-com';

export default {
  name: 'ContactForm',
  data() {
    return {
      electricBlue: "#4EBBC2",
      darkElectricBlue: "#557F82",
      smoothBlack: "#1F2222",
      smoothWhite: "#FFF9F3",
      formData: {
        name: '',
        email: '',
        subject: '',
        request: ''
      },
      messageSuccess: false,
      messageError: false
    };
  },
  methods: {
    submitForm() {
      emailjs.send("service_eukhgde","template_dys7ilc", this.formData)
          .then(() => {
            console.log('E-mail envoyé avec succès !');
            this.messageSuccess = true;
            this.messageError = false;
          })
          .catch(error => {
            console.error('Erreur lors de l\'envoi de l\'e-mail:', error);
            this.messageSuccess = false;
            this.messageError = true;
          });
    },
  },
  computed: {
    ...mapState(['isNightMode'])
  }
};
</script>

<style scoped>
form, form div {
  display: flex;
  flex-direction: column;
  align-items: center;
}
form{
  gap: 1rem;
}
h2, label, figcaption {
  color: v-bind(smoothWhite);
}
label{
  margin-bottom: .8rem;
}
input[type="text"], input[type="email"], select{
  width: 75vw;
  height: 5vh;
  background-color: v-bind(smoothWhite);
  caret-color: v-bind(electricBlue);
  padding: 0 1rem;
}
textarea {
  width: 75vw;
  height: 20vh;
  background-color: v-bind(smoothWhite);
  caret-color: v-bind(electricBlue);
  padding: 1rem;
}
button[type="submit"] {
  cursor: pointer;
  margin-top: .8rem;
  color: v-bind(smoothBlack);
  background-color: v-bind(electricBlue);
  min-height: 10vh;
  min-width: 15vh;
  border: 0;
  border-radius: 10%;
  transition: 0.5s ease-in-out;
}
button[type="submit"]:disabled {
  color: v-bind(smoothWhite);
  background-color: v-bind(darkElectricBlue);
  cursor: not-allowed;
}
</style>
